	module.exports = {"data":{"markdownRemark":{"html":"<h2>Hr Tag</h2>\n<p>The horizontal rule or <code>&#x3C;hr></code> is a tag that allows to insert a divisory line, defining the content of your document.\nIt’s very important to clarify that this tag is a thematic break; in previous versions of .html is a horizontal rule.</p>\n<h3>Example</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code><!DOCTYPE html>\n<html>\n  <body>\n    <h2>FreeCodeCamp</h2>\n    <p>FreeCodeCamp is a place where you can learn to code from scratch to professional</p>\n\n    <hr>\n\n    <h3>How to start</h3>\n    <p>Just go to <a href=\"www.freecodecamp.com\">FreeCodeCamp website and start learning!</a></p>\n\n    <hr>\n  </body\n</html></code></pre>\n      </div>","fields":{"slug":"/html/elements/hr-tag/"},"frontmatter":{"title":"Hr Tag"}}},"pathContext":{"slug":"/html/elements/hr-tag/"}}

